<template>
  <div>
    <div class="table" id="out-table">
      <div class="container1">
        <el-table :data="tableData" :stripe="true" style="width: 100%">
          <el-table-column prop="index" label="">
          </el-table-column>
          <el-table-column prop="date" label="">
          </el-table-column>
          <el-table-column prop="sf" label="()">
          </el-table-column>
          <el-table-column prop="yc" label="()">
          </el-table-column>
          <el-table-column prop="jd" label="(%)">
          </el-table-column>
          <el-table-column prop="zxs" label="()">
          </el-table-column>
          <el-table-column prop="zxx" label="()">
          </el-table-column>
          <el-table-column prop="xd" label="()">
          </el-table-column>
        </el-table>
      </div>
    </div>
    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes=pageSizeArray :page-size=pageSize layout="total, sizes, prev, pager, next" :total=pageTotal>
    </el-pagination>
  </div>
</template>

<script>
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
  data() {
    return {
      tableData: [
        // {
        //   index: 1,
        //   date: "2018-07-04 16:59:38",
        //   sf: "47.2465",
        //   yc: "56.6958",
        //   jd: "5%",
        //   zxs: "37.2465",
        //   zxx: "27.2465",
        //   xd: "17.2465"
        // },
        // {
        //   index: 2,
        //   date: "2018-07-04 16:59:38",
        //   sf: "47.2465",
        //   yc: "56.6958",
        //   jd: "5%",
        //   zxs: "37.2465",
        //   zxx: "27.2465",
        //   xd: "17.2465"
        // },
        // {
        //   index: 3,
        //   date: "2018-07-04 16:59:38",
        //   sf: "47.2465",
        //   yc: "56.6958",
        //   jd: "5%",
        //   zxs: "37.2465",
        //   zxx: "27.2465",
        //   xd: "17.2465"
        // },
        // {
        //   index: 4,
        //   date: "2018-07-04 16:59:38",
        //   sf: "47.2465",
        //   yc: "56.6958",
        //   jd: "5%",
        //   zxs: "37.2465",
        //   zxx: "27.2465",
        //   xd: "17.2465"
        // },
        // {
        //   index: 5,
        //   date: "2018-07-04 16:59:38",
        //   sf: "47.2465",
        //   yc: "56.6958",
        //   jd: "5%",
        //   zxs: "37.2465",
        //   zxx: "27.2465",
        //   xd: "17.2465"
        // }
      ],
      pageTotal: 100,
      pageSizeArray: [20, 30, 40, 50],
      pageSize: 30,
      currentPage: 1
    };
  },
  components: {},
  mounted() {
    let that = this;
    that.$http
      .get(this.BASE_INFO.BASE_URL + "api/users", {})
      .then(function(response) {
        that.tableData = response.data.tableData;
      });
  },
  methods: {
    tab(flag) {
      if (flag == 0) {
        this.$router.push("/oversee/PVview");
      } else if (flag == 1) {
        this.$router.push("/oversee/PVdata");
      }
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    exportExcel() {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector("#out-table"));
      /* get binary string as output */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          "sheetjs.xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    }
  }
};
</script>

<style>
.subTitle {
  font-size: 18px;
  font-family: "Microsoft YaHei UI, Regular";
  margin-bottom: 15px;
}
.container1 .el-table th {
  background: #e4e7eb !important;
  font-size: 14px;
  font-family: "Microsoft YaHei, Bold";
  text-align: center;
}
.container1 .el-table tr {
  height: 50px;
  background: #f5f4f7 !important;
  font-family: "Arial, Regular";
  text-align: center;
}
</style>



